footer {
	text-align:left;
	width:100%;
	min-width: $min-width;
	order: 3;	
    -webkit-order: 3;	
}

.footer {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center top;
	position: relative;
	padding-top: 405px;
	text-align: center;
	&-bottom {
		margin-top: 40px;
    	padding: 30px 0;
		border-top: 1px solid rgba($color: #FFFFFF, $alpha: 0.1);
		color: #B3B1B7;
		font-size: 14px;
	}
	&-logo {
		max-width: 150px;
	}
}

