
//main styles

.main-wrapper{
	padding: 0 0 0 0;
	min-width: $min-width;
    width: 100%;
	position: relative;
	overflow: hidden;
	min-height:100%;
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	flex-direction:column;
    -webkit-flex-direction:column;
    -ms-flex-direction: column;
	justify-content:flex-start;	
    -webkit-justify-content:flex-start;	
    -ms-flex-pack: start;
}
.wrapper{
	min-width: $min-width;
	max-width: $max-width;
	padding: 0 40px 0 40px;
	margin: 0 auto;
	position: relative;
}

/* titles */
p {	
	@include font($base-font-size,$base-line-height,$text-color);
	font-weight: 400;	
}

h2, .title2 {
	font-weight: 600;
	font-size: 50px;
	line-height: 100%;
	font-family: 'DarkerGrotesque', sans-serif;
	text-transform: capitalize;
	position: relative;
	z-index: 1;
}
h3, .title3 {
	font-weight: 600;
	font-size: 36px;
	line-height: 100%;
	font-family: 'DarkerGrotesque', sans-serif;
	text-transform: capitalize;
}

/* text position */
.text-left{text-align: left!important}
.text-center{text-align: center!important}
.text-right{text-align: right!important}
.nowrap{white-space: nowrap!important;}


		/* loader */
.loaded .main-wrapper{visibility:hidden;}
.icon-load{background:url(../img/loader.gif) no-repeat left top;width:40px;height:40px;position:fixed;left:50%;top:50%;margin-left:-20px;margin-left:-20px;display:none;}
.loaded .icon-load{display:block;}


/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content {	
	min-width: $min-width;
	text-align:left;
    width: 100%;
	order: 2;
    -webkit-order: 2;
	flex-grow: 1;
    -webkit-flex-grow: 1;
}

.btn {
	display: inline-block;
	transition: $transition;
	outline: none;
	border: none;
	font-weight: 500;
	font-size: 16px;
	line-height: 170%;
	color: #FFFFFF;
	text-transform: capitalize;
	box-shadow: 0px 5px 10px rgba(252, 104, 58, 0.25);
	border-radius: 30px;
	position: relative;
	font-family: $base-font-family;
	padding: 16px 35px;
	text-overflow: ellipsis;
    white-space: nowrap;
	span {
		position: relative;
		z-index: 2;
	}
	&:after, &:before {
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		border-radius: 30px;
		transition: $transition;
	}
	&:before {
		background: linear-gradient(270deg,rgba(255,169,121,.991703) 0,rgba(255,98,49,.991703) 100%);
	}
	&:after {
		opacity: 0;
		z-index: 1;
		background: linear-gradient(270deg,rgba(255,98,49,.991703) 0,rgba(255,169,121,.991703) 100%);
	}
	&:hover {
		box-shadow:0 5px 20px rgba(252,104,58,.45%);
    	color: #fff;
		&:after {
			opacity: 1;
		}
	}

	&-stroke {
		padding: 3px 19px;
		background: transparent;
		color: #FF6231;
		border: 2px solid #FF6231;
		box-sizing: border-box;
		border-radius: 20px;
		box-shadow: none;
		&:hover {
			background: #ff6231;
			color: #fff;
			opacity: 1;
			box-shadow: none;
		}
		&:before, &:after {
			display: none;
		}
	}
}

.title-bg {
	font-size: 40px;
	line-height: 47px;
	text-transform: capitalize;
	color: #CCCCCC;
	font-family: 'Codystar', sans-serif;
	position: absolute;
	left: 0;
    bottom: 5px;
	z-index: -1;
	font-weight: 300;
}

// main block

.main-block {
    padding: 114px 0 208px;
	background-image: url(../img/main-block__bg.svg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	position: relative;
	&__wave {
		background-image: url(../img/main-block__wave.svg);
		min-width: 1920px;
		width: 100%;
		position: absolute;
		bottom: 17px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 2;
		background-repeat: no-repeat;
	    background-size: cover;
    	background-position: center;
		height: 125px;
	}
	&__back-text {
		position: absolute;
	    left: -11px;
    	bottom: -8px;
		font-size: 220px;
		line-height: 260px;
		font-family: 'Codystar', sans-serif;
		text-transform: capitalize;
		color: #CCCCCC;
		opacity: 0.2;
	}
	&__content {
		display: flex;
	}
	&__info {
		max-width: 538px;
	}
	&__top {
		display: flex;
		align-items: center;
		margin-bottom: 41px;
		img {
			margin-right: 48px;
		}
	}
	&__subtext {
		font-weight: 500;
		font-size: 28px;
		line-height: 110%;
		color: #fff;
		font-family: 'DarkerGrotesque', sans-serif;
	}
	&__img {
		position: absolute;
		top: -28%;
		right: -12.3%;
	}
}

// look block

.look-block {
	position: relative;
	padding: 176px 0 140px;
	z-index: 1;
	&__bg {
		position: absolute;
		left: 50%;
	    top: 17px;
		transform: translateX(-50%);
		max-width: initial;
		z-index: -1;
	}
	h2 {
		margin-bottom: 50px;
		&:before, &:after {
			position: absolute;
			content: '';
			width: 6px;
			height: 6px;
			border-radius: 100%;
			background: #FF6231;
			top: -20px;
			left: 0;
		}
		&:after {
			left: 10px;
		}
	}
	&__items {
		display: flex;
		flex-wrap: wrap;
		margin-left: -15px;
		margin-right: -15px;
	}
	&__item {
		margin: 0 15px;
		width: calc(50% - 30px);
		margin-bottom: 70px;
		text-align: center;
		color: #000000;
		&-img {
			width: 100%;
			height: 430px;
			border-radius: 20px;
			box-shadow: 0px 10px 20px rgba(47, 21, 135, 0.06);
		    margin-bottom: 21px;
			overflow: hidden;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 20px;
				transition: $transition;
			}
		}
		&:hover {
			.look-block__item-img {
				img {
					transform: scale(1.02);
				}
			}
		}
	}
}

// subscribe-block

.subscribe-block {
	background: #FFFFFF;
	box-shadow: 0px 30px 20px rgba(47, 21, 135, 0.15);
	border-radius: 20px;
	width: 100%;
	position: relative;
	margin-bottom: -360px;
	z-index: 1;
	&__img {
		position: absolute;
	    left: 29px;
		bottom: 0;
		overflow: hidden;
		width: 396px;
		height: 100%;
		img {
			bottom: 0;
			display: block;
			position: absolute;
		}
		&-decor {
		   left: 0;
		}
		&-item2 {
			left: 36px;
		}
		&-item1 {
			right: 0;
		}
	}
	&__text {
		padding: 80px 0 70px;
		padding-left: 489px;
		padding-right: 40px;
		&-top {
			padding-left: 30px;
			position: relative;
			font-weight: 500;
			font-size: 12px;
			line-height: 170%;
			letter-spacing: 0.1em;
			text-transform: uppercase;
			color: #FF6231;
			display: inline-block;
			&:before, &:after {
				position: absolute;
				content: '';
				width: 6px;
				height: 6px;
				border-radius: 100%;
				background: #FF6231;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
			}
			&:after {
				left: 10px;
			}
		}
		p {
			max-width: 438px;
			color: #888888;
			margin-top: 14px;
		}
		.btn {
			margin-top: 26px;
		}
	}
}

// media


@media screen and (max-width: 1200px) {
	.look-block {
		padding: 60px 0;
	}
}

@media screen and (max-width: 991px) {
	.subscribe-block__img {
		position: relative;
		left: auto;
		bottom: 0;
		width: 396px;
		height: 396px;
		margin: auto;
	}
	.subscribe-block {
		display: flex;
		flex-direction: column-reverse;
	}
	.subscribe-block__text {
		padding: 30px 15px 0;
		text-align: center;
	}
	.look-block__item-img {
		height: 315px;
	}
	.main-block__content {
		flex-direction: column;
	}
	.main-block__img {
		position: relative;
		top: auto;
		right: auto;
		margin-top: 30px;
	}
	.main-block__back-text {
		display: none;
	}
	.main-block {
		padding: 80px 0 135px;
	}
	.main-block__wave {
		height: 210px;
	}
}

@media screen and (max-width: 767px) {
	.wrapper {
		padding: 0 15px 0 15px;
	}
	.look-block__item {
		margin: 0 7.5px 40px;
		width: calc(50% - 15px);
	}
	.look-block__items {
		margin-left: -7.5px;
		margin-right: -7.5px;
	}
	.look-block__item-img {
		height: 250px;
	}
}

@media screen and (max-width: 480px) {
	.look-block__items {
		margin-left: 0;
		margin-right: 0;
	}
	.look-block__item {
		width: 100%;
		margin-left: 0;
		margin-right: 0;
		margin-bottom: 40px;
		&-img {
			height: 370px;
		}
	}
	.subscribe-block__img {
		width: 100%;
	}
	.main-block__subtext {
		font-size: 22px;
	}
	.main-block__top {
		flex-direction: column;
		img {
			margin-right: 0;
			margin-bottom: 30px;
		}
	}
	.look-block {
		padding: 30px 0;
		margin-top: 30px;
	}

	.main-block__wave {
		height: 210px;
	}
}



